<template>
    <div id="dashboard" >
        <blockquote class="flow-text trans lighten-2 dark-text">
            {{ quote }}
        </blockquote>

        <ul class="collection with-header">
            <li class="collection">
                <h4>Shopping Items</h4>
            </li>
            <li class="collection-item">
                <table class="striped highlight deeper-blue responsive-table">
                    <thead>
                    <tr>
                        <th>Item Name</th>
                        <th>Item Price</th>
                        <th>Quantity</th>
                        <th>Price</th>
                    </tr>
                    </thead>

                    <tbody v-if="items.length > 0">
                    <tr v-for="item in items" v-bind:key="item.id" >
                        <td>{{item.item_name}}</td>
                        <td>{{item.price}}</td>
                        <td>{{item.quantity}}</td>
                        <td v-once>{{ item.quantity * item.price }}</td>
                        <td class="centered">
                            <router-link style="float:left;margin-right:-35px;margin-left: 40px" class="secondary-content" v-bind:to="{name: 'edit-item', params: {item_id: item.item_id}}">
                                <span><i class="fa fa-edit" style="color: blue; font-size: 20px;"></i></span>
                            </router-link>
                            
                        
                            <span class="cursor hoverable" @click="deleteItem(item.item_id)"><i class="fa fa-trash" style="float:right;margin-left:-25px;margin-right:30px;color: red; font-size: 20px;"></i></span>
                        </td>   
                    </tr>
                    </tbody>
                    <tbody v-else>
                        <p class="center">Empty Item list</p>     
                    </tbody>
                </table>
            </li>
            <li class="collection-item center">
                <h3>Total: <span>{{ total }}</span></h3>
            </li>
        </ul>

        <!-- floating plus sign -->
        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large deep-purple">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<style scoped>
    blockquote {
        margin: 20px 0;
        padding-left: 1.5rem;
        border-left: 5px solid #4527a0;
    }
    .trans {
        opacity: 0.7;
    }
    .cursor:hover {
        cursor: pointer;
    }
   
</style>

<script>
import db from './firebaseInit'
export default {
    name: "dashboard",
    data () {
        return {
            quote: "The Journey of a thousand mile begins with a step",
            items: [],
            tP: 0,
        }
    },
    created () {
        db.collection('shopListing').orderBy("item_id", "desc").get().then(querySnapShot => {
            querySnapShot.forEach(doc => {
                console.log(doc.data())
                const shopListingData = {
                    id: doc.id,
                    item_id: doc.data().item_id,
                    item_name: doc.data().item_name,
                    price: doc.data().price,
                    quantity: doc.data().quantity
                }
                console.log(shopListingData)
                this.items.push(shopListingData)
            })
        })
    },
    methods: {
        deleteItem(item_id) {
            console.log("Trash clicked")
            if (confirm("Are you sure?")) {
                db.collection('shopListing').where('item_id', '==', item_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.delete()
                    this.$router.push('/')
                })
            })
            this.$router.push('/')
            }
        }
    },
    computed: {
        total() {
            for (var i = 0; i < this.items.length; i++) {
                this.tP += this.items[i].price*this.items[i].quantity
            }
            return this.tP
        },
    }
}
</script>


