<template>
    <div id="new-item">
        <h4>New Item</h4>
        <form @submit.prevent="saveItem" class="col s12">
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="item_id" min=0 required>
                    <label for="autocomplete-input">
                        ID
                    </label>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="item_name" required>
                    <label for="autocomplete-input">
                        Item Name
                    </label>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="quantity" min=0 required>
                    <label>
                        Quantity
                    </label>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="price" min=0 required>
                    <label>Price (FCFA)</label>
                </div>
            </div>
            <button type="submit" class="btn-large waves-light hoverable  deep-purple large">Submit</button>
            <router-link to="/" class="btn-large hoverable white purple-text text-darken-4">Cancel</router-link>
        </form>
    </div>
</template>

<script>
import db from "./firebaseInit"
export default {
    name: "new-item",
    data () {
        return {
            item_id: null,
            item_name: null,
            price: null,
            quantity: null
        }
    },
    methods: {
        saveItem() {
            db.collection('shopListing').add({
                item_id: this.item_id,
                item_name: this.item_name,
                quantity: this.quantity,
                price: this.price
            })
            .then(docRef => this.$router.push('/'))
            .catch(error => console.log(err))
        }
    }
}
</script>


