<template>
    <div id="edit-item">
        <h4>Edit Item - "{{item_name}}"</h4>
        <form @submit.prevent="editItem" class="col s12">
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="item_id" disabled>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="item_name" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="quantity" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="price" required>
                </div>
            </div>
            <button type="submit" @click="updateItem" class="btn-large waves-light hoverable  deep-purple large">Submit</button>
            <router-link to="/" class="btn-large hoverable white purple-text text-darken-4">Cancel</router-link>
        </form>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: "edit-item",
    data () {
        return {
            item_id: null,
            item_name: null,
            price: null,
            quantity: null
        }
    },
    beforeRouteEnter (to, from, next) {
        // item_id in to.params.item_id is referenced in the router
        db.collection('shopListing').where('item_id', '==', to.params.item_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(fields => {
                    fields.item_id = doc.data().item_id
                    fields.item_name = doc.data().item_name
                    fields.price = doc.data().price
                    fields.quantity = doc.data().quantity
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData() {
            db.collection('shopListing').where("item_id", "==", this.$route.params.item_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.item_id = doc.data().item_id
                    this.item_name = doc.data().item_name
                    this.price = doc.data().price
                    this.quantity = doc.data().quantity
                })
            })
        },
        updateItem() {
            db.collection('shopListing').where("item_id", "==", this.$route.params.item_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        item_id: this.item_id,
                        item_name: this.item_name,
                        quantity: this.quantity,
                        price: this.price
                    })
                    .then(() => {
                        this.$router.push("/")
                    })
                })
            })
        }
    }
}
</script>


